var div: HTMLDivElement = document.createElement('div')
div.style.border = 'solid 1px red'
div.style.width = '100px'
div.style.height = '100px'
document.body.appendChild(div)

var isDragging: boolean = false
var position: [number, number] = [0, 0]

div.onmousedown = (e) => {
    isDragging = true
    position = [e.clientX, e.clientY]
}

document.onmouseup = (e) => {
    isDragging = false
}

document.onmousemove = (e) => {
    if (isDragging == true) {
        var detalX = e.clientX - position[0]
        var detalY = e.clientY - position[1]
        var left = parseInt(div.style.left!) || 0
        var top = parseInt(div.style.top!) || 0
        var resultX = left + detalX
        var resultY = top + detalY
        if (resultX < 0) {
            resultX = 0
        }
        if (resultY < 0) {
            resultY = 0
        }
        div.style.left = resultX + 'px'
        div.style.top = resultY + 'px'
        position = [e.clientX, e.clientY]
    }
}
